import * as React from "react";
import './todo.css';

export interface TodoItemProps {
  description: string;
  expectedTime: string;
}
export interface TodoItemState { complete: boolean; }

export class TodoItem extends React.Component<TodoItemProps, TodoItemState> {
  constructor(props: TodoItemProps) {
    super(props);
    this.state = { complete: false }
  }
  activate(): void {
    this.state = { complete: !this.state.complete}
    console.log(this.state);
  }
  render() {
    return <div className={this.state.complete ? "complete": null} onClick={this.activate.bind(this)}>
    {this.props.description} <i>{this.props.expectedTime}</i>
    </div>;
      }
}



